}

.down-arrow {
  position: absolute;
  right: 0;
}

#adv-search {
  position: relative;
}

.advace-drop {
  z-index: 99;
  position: absolute;
  /* width: 500px; */
  width: 100%;
  top: 42px;
  background: #fff;
  border: solid 0.5px rgba(151, 151, 151, 0.16);
  padding: 34px 16px 19px 16px;
  box-shadow: 5px 6px 12px 0 rgba(0, 0, 0, 0.11);
}

.dropdown-toggle {
  position: absolute;
  right: 8px;
  top: 5px;
}

.btn.focus,
.btn:focus {
  outline: 0;
  box-shadow: none;
}

.overlay1 {
  display: none;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0);
  z-index: 84;
  opacity: 0;
  transition: all 0.5s ease-in-out;
  left: 0;
  top: 0;
}

.overlay1.active {
  display: block;
  opacity: 1;
}

#adv-search {
  /* width: 100%; */
  width: 85%;
  margin: 0 auto;
}

.mat-flat-button {
  color: rgba(0, 0, 0, 0.87);
  background-color: #fff;
  width: 170px;
  margin-right: 0px;
  border: solid 0.4px #979797;
}

.mat-flat-button.mat-accent {
  background-color: #ff6400;
  width: 170px;
  color: #fff;
  border: none;
}

.headersrch {
  background-image: url(../../../assets/img/search-icon.png);
  background-repeat: no-repeat;
  background-position: 8px 11px;
  padding-left: 32px;
}

.form-control.headersrch {
  height: 40px;
  border: solid 0.5px rgba(151, 151, 151, 0.16);
  border-radius: 5px;
}

#adv-search .mat-icon-button {
  /* margin-right: 21px; */
  margin-right: 0px;
}

