/* } */

.dropdown-toggle::after {
  display: inline-block;
  margin-left: 0.255em;
  vertical-align: 0.255em;
  content: "";
  border-top: 0.3em solid;
  border-right: 0.3em solid transparent;
  border-bottom: 0;
  font-size: 18px;
  color: #7d7d7d;
  border-left: 0.3em solid transparent;
  margin-top: 10px;
}

.username {
  width: 267px;
  position: fixed;
  right: 9px;
  top: 18px;
}

.logoicon1 {
  outline: none;
  cursor: pointer;
  width: 105px;
  margin-left: 10px !important;
}

.mat-menu-item {
  line-height: 52px;
  height: auto;
  padding-top: 5px;
}

.noti-menu {
  line-height: 18px;
  word-break: break-word;
  white-space: normal;
}

.noti-text {
  font-size: 11px;
  margin-bottom: 0px;
  font-family: "GothamNarrowBold", sans-serif;
}

