}

.timeclock img {
  margin-right: 8px;
}

.read {
  background: #f5f7f9 !important;
}

.mat-badge-medium.mat-badge-overlap.mat-badge-after .mat-badge-content {
  right: -8px;
  top: -11px;
  width: 28px;
  height: 28px;
  line-height: 28px;
}

.mat-badge-content {
  color: #fff;
  background: #1072ce;
}

.dsktop-logo {
