  font-size: 12px;
  font-family: "GothamNarrowBold", sans-serif;
}

.noti-item:hover {
  background-color: #f5f7f9 !important;
  color: #000 !important;
}

.noti-list-item {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  outline: 0;
  border: none;
  -webkit-tap-highlight-color: transparent;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  line-height: 48px;
  height: 48px;
  padding: 0 16px;
  text-align: left;
  text-decoration: none;
  max-width: 100%;
  position: relative;
}

.noti-list-item {
  line-height: 52px;
  height: auto;
  padding-top: 5px;
}

.noti-item {
  cursor: pointer;
}

::ng-deep .customClass .mat-menu-content .mat-menu-item {
